. ")]),_c('p',[_vm._v(" Подразумевается, что в выписке правообладатели не указаны или указаны, но неверно. Произойти это может по многим причинам. Рассмотрим несколько из них. Во-первых, собственник мог законно приватизировать жилплощадь прежде, чем наступил 1998 год. Затем, должной регистрации прав не последовало. Спустя время квартира переходит кому-либо в качестве наследства. Новый владелец тоже не регистрирует собственность. Несмотря на то, что он имеет все основания на получение наследства, в выписке не укажут ни одного владельца. Во-вторых, в необходимом документе может присутствовать исключительно предыдущий владелец. Здесь он смог зарегистрировать имущество в Росреестре, но вот его наследник этого не сделал. В такой ситуации в нужной выписке отобразится только предыдущий владелец. ")]),_c('p',[_vm._v(" 6. По одному адресу существует не один номер кадастра. ")]),_c('p',[_vm._v(" Несостыковка в базе реестра могла возникнуть, как во время разделения объекта, так и из-за сложного адреса, который был указан неправильно. ")]),_c('p',[_vm._v(" 7. Ошибка сайта или неточность номера кадастра. ")]),_c('p',[_vm._v(" Иногда владелец оказывается невиновным в отсутствии верных и актуальных сведений в реестре Ошибки происходят из-за проведенных кадастровых работ или при передаче информации из БТИ. Даже в самом Росреестре возникают сбои. Информация, которая должна поступить в реестр недвижимости из этого портала, может оказаться неверной. При обнаружении несоответствий необходимо написать обращение в Росреестр или МФЦ. После обращения будут исправлены и технические, и кадастровые ошибки. ")])])]),_c('div',{staticClass:"questions__item"},[_c('div',{staticClass:"questions__item-title"},[_vm._v(" Как убедиться в подлинности и наличии ЭЦП? "),_c('div',{staticClass:"questions__icon-plus"})]),_c('div',{staticClass:"questions__item-content"},[_c('p',{staticClass:"questions__text--mb"},[_vm._v(" Согласно нормативным актам проверка любых ЭЦП должна осуществляться в удостоверяющих це